import React from 'react';
import Left from './left';
import Timeline from './timeline'
import { FormattedMessage } from 'react-intl';

export interface PropProperties {
    cv: any,
    print: boolean,
    setPage: (p: number) => void,
}

const PageOne: React.FC<PropProperties> = (props: PropProperties) => {

    const { cv, setPage, print } = props;

    return (
        <article className="active" onClick={() => setPage(2)}>
            <div className="row">
                {
                    !print ?
                        <div className="col-3">
                            <Left klassName="col-md-3-3" cv={cv} />
                        </div>
                        : ''
                }

                <div className={`right ${print ? 'col-12' : 'col-9'}`}>
                    {
                        print ?
                            <section className="persional">
                                <div className="title-wrap">
                                    <i className="fa fa-user"></i>
                                    <span><FormattedMessage id="persional_info" /></span>
                                </div>
                                <div className="content">
                                    {
                                        print && cv.contact && cv.contact.address ?
                                            <div>
                                                {
                                                    cv.contact.address.map((info: any, i: number) => (
                                                        <div className="information" key={`contact-address--${i}`}>
                                                            <span>
                                                                <i className={info.icon}></i>&nbsp;
                                                            {
                                                                    info.link ?
                                                                        <a href={info.link} target="_blank" rel="noreferrer">{info.desc}</a>
                                                                        :
                                                                        <span>{info.desc}</span>
                                                                }
                                                            </span>
                                                        </div>
                                                    ))
                                                }

                                            </div>
                                            : ""
                                    }
                                    <div>
                                        {
                                            print && cv.socials && cv.socials.map((social: any, i: number) => (
                                                <div className="information" key={`social--${i}`}>
                                                    {
                                                        social.link ?
                                                            <span>
                                                                <i className={social.icon}></i>&nbsp;
                                                        <a href={social.link} target="_blank" rel="noreferrer">{social.desc}</a>
                                                            </span>
                                                            :
                                                            <span>{social.desc}</span>
                                                    }
                                                </div>
                                            ))
                                        }
                                    </div>
                                </div>
                            </section>
                            : ""
                    }
                    {
                        cv.educations ?
                            <section className="education">
                                <div className="title-wrap">
                                    <i className="fa fa-book"></i>
                                    <span><FormattedMessage id="educations" /></span>
                                </div>
                                <div className="mt-3 ml-4">
                                    {
                                        cv.educations.map((education: any, i: number) => (

                                            Object.values(education).map((value: any, j: number) => (
                                                <span key={`educations----${i}-${j}`}>{value}&nbsp;</span>
                                            ))
                                        ))
                                    }
                                </div>
                            </section>
                            : ""
                    }
                    {
                        cv.works ?
                            <section className="works">
                                <div className="title-wrap">
                                    <i className="fa fa-briefcase"></i>
                                    <span><FormattedMessage id="works" /></span>
                                </div>
                                <div className="content">
                                    <Timeline items={cv.works} page={1} print={print} />
                                </div>
                            </section>
                            : ""
                    }
                </div>
            </div>
        </article >
    );
}

export default PageOne;
