import React from 'react';
import vi from '../static/vi.svg';
import en from '../static/en.svg';

interface PropProperties {
    print: boolean;
    language: string,
    doPrint: (event: any) => void,
    setLanguage: (language: string) => void,
}

const Toolbar: React.FC<PropProperties> = (props: PropProperties) => {

    const { print, language, doPrint, setLanguage } = props;

    return (
        <>
            { !print ?
                <aside className="vc--toolbar">
                    <ul>
                        <li>
                            <span className="cursor p-3 d-block" onClick={doPrint}><i className="fa fa-print"></i>&nbsp;</span>
                        </li>
                        <li onClick={() => setLanguage(language === 'vi' ? 'en' : 'vi')}>
                            <span className="cursor p-3 d-block">
                                <img className="img-thumbnail vc--resume-flag" src={language === 'vi' ? en : vi} alt="" />
                            </span>
                        </li>
                    </ul>
                </aside>
                : ""
            }
        </>
    );
}

export default Toolbar;
