import React from 'react';
import Avatar from './avatar'

interface PropProperties {
    cv: any;
    print: boolean
}

const Header: React.FC<PropProperties> = (props: PropProperties) => {

    const { cv, print } = props;

    return (
        <>
            {
                print ?
                    <header className="vc--resume-header col-12">
                        <div className="row">
                            {
                                cv.contact ?
                                    <>
                                        <div className="contact-info col-4">
                                            {
                                                cv.contact.avatar ?
                                                    <Avatar src={cv.contact.avatar} />
                                                    : ''
                                            }
                                        </div>
                                        <section className="title col-8">
                                            {
                                                cv.contact.name ?
                                                    <div className="user-name">
                                                        {cv.contact.name}
                                                    </div>
                                                    : ''
                                            }
                                            {
                                                cv.job.position.desc ?
                                                    <div className="job-title">
                                                        {cv.job.position.desc}
                                                    </div>
                                                    : ''
                                            }
                                        </section>
                                    </>
                                    : ""
                            }
                        </div>

                    </header>
                    : ""
            }
        </>
    );
}

export default Header;
