import React, { useEffect, useState } from 'react';
import { IntlProvider } from 'react-intl'
import viMessage from './../lang/vi.json';
import enMessage from './../lang/en.json';
import { request } from './../service/request'
import Content from './../components/content'
import Print from './../components/print'
import Toolbar from './../components/toolbar'
import TopCV from './../static/resume.json'

const jsonUri = (window as any).__URL__ || null;

const App: React.FC = () => {

  const defaultProfile = Object.create(TopCV);

  const [print, setPrint] = useState(false);
  const [language, setLanguage] = useState('vi');
  const [profile, setProfile] = useState(defaultProfile);
  const [page, setPage] = useState(1);
  const [languageMessage, setLanguageMessage] = useState(viMessage);
  const [cv, setCv] = useState(defaultProfile.vi);
  //listen on end print event
  (window as any).onafterprint = (event: any) => { setPrint(false) };

  //mount
  useEffect(() => {
    if (jsonUri) {
      request(jsonUri)
        .then((profile) => {
          if (profile) {
            setProfile(profile);
          }
        })
        .catch((e) => {
        })
    }
  }, []);

  //change language
  useEffect(() => {
    setLanguageMessage(language === 'vi' ? viMessage : enMessage);
  }, [language]);

  //change profile by language
  useEffect(() => {
    setCv((profile && profile[language]) ? profile[language] : defaultProfile.vi);
  }, [profile, language, defaultProfile]);

  //print effect
  useEffect(() => {
    if (print) {
      (window as any).print();
    }
  }, [print])

  return (
    <IntlProvider messages={languageMessage} locale={language} defaultLocale="vi">
      <div className="vc--resume">
        <div className="row">
          <div className="vc--resume col-12">
            <>
              {
                print ?
                  <Print cv={cv} page={page} print={print} setPage={(p) => setPage(p)} />
                  :
                  <Content cv={cv} page={page} print={print} setPage={(p) => setPage(p)} />
              }
            </>
          </div>
          <Toolbar print={print}
            language={language}
            doPrint={(event: any) => { setPrint(true) }}
            setLanguage={(lang) => setLanguage(lang)}
          />
        </div>
      </div >
    </IntlProvider>
  );
}

export default App;
