import React from 'react';
import Header from './header'
import Footer from './footer'
import PageOne from './pageOne'
import PageTwo from './pageTwo'

interface PropProperties {
    cv: any;
    page: number,
    print: boolean,
    setPage: (page: number) => void
}

const Content: React.FC<PropProperties> = (props: PropProperties) => {

    const { cv, page, print, setPage } = props;

    return (
        <main className="vc--resume-content row">
            <Header cv={cv} print={print} />
            {
                page === 1 ?
                    <PageOne cv={cv} setPage={() => setPage(2)} print={print} />
                    :
                    <PageTwo cv={cv} setPage={() => setPage(1)} print={print} />
            }
            <Footer page={page} setPage={(p) => setPage(p)} print={print} />
        </main >
    );
}

export default Content;
