import React from 'react';
import Left from './left'
import Timeline from './timeline'
import { FormattedMessage } from 'react-intl';

interface PropProperties {
    cv: any;
    print: boolean,
    setPage: (p: number) => void,
}

const pageTwo: React.FC<PropProperties> = (props: PropProperties) => {

    const { cv, setPage, print } = props;

    return (
        <article className="active" onClick={() => setPage(1)}>
            <div className="row">
                {
                    !print ?
                        <div className="col-3">
                            <Left klassName="col-md-3-3" cv={cv} />
                        </div>
                        : ''
                }
                <div className={`right ${print ? 'col-12' : 'col-9'}`}>
                    {
                        cv.projects ?
                            <section className="projects">
                                <div className="title-wrap">
                                    <i className="fa fa-file-text"></i>
                                    <span><FormattedMessage id="projects" /></span>
                                </div>
                                <div className="content">
                                    <Timeline items={cv.projects} page={2} print={print} />
                                </div>
                            </section>
                            : ""
                    }
                    {
                        cv.skillsDesc ?
                            <section className="skill-desc">
                                <div className="title-wrap">
                                    <i className="fa fa-wrench"></i>
                                    <span><FormattedMessage id="skills" /></span>
                                </div>
                                <div className="content">
                                    <ul className="skill-desc">
                                        {
                                            cv.skillsDesc && cv.skillsDesc.map((skill: any, i: any) => (
                                                <li key={`skill--desc-${i}`}>
                                                    <i className="fa fa-certificate"></i>&nbsp;{ skill}
                                                </li>
                                            ))
                                        }
                                    </ul>
                                </div>
                            </section>
                            : ""
                    }
                </div>
            </div>
        </article>
    );
}

export default pageTwo;
